@import "_component/layout/reset.scss";

$alb: #ffffff;
$negru: #000000;
$fruit-salad: #4caf50;
$seashell: #f1f1f1;
$scorpion: #5f5f5f;

html ,body {
    height: 100%;
}

a {
    text-decoration: none;
    padding: 15px;
    // height: 0px;
    color: $negru;
    background-color: $alb;
}
header div {
    display: inline-block;   
}
header a {
    background-color: $scorpion;
    color: white;
    display: inline-block;
    height: 100%;
}
nav a {
    display: block;
}

body {
    display: grid;
    grid-template-columns: 200px 1fr;
    grid-template-rows: 40px 1fr 40px;
    font-family: "Roboto";
}
header {
    background-color: $scorpion;
    grid-row: 1/2;
    grid-column: 1/3;
}

nav {
    padding: 0 15px;
    grid-column: 1/2;
    grid-row: 2/3;
    background-color: $seashell;
    // height: 700px;
    padding: 20px 20px;
}

footer {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-row: 3/4;
    grid-column: 1/3;
    background-color: $scorpion;
    color: white;
}

article {
    background: url("asset/image/company-concept-creative-7369.jpg");
    background-size: cover;
    background-position: center, center;
    padding: 20px;
    background-color: white;
    grid-column: 2/3;
    grid-row: 2/3;  
}
p {
    font-weight: 400;
}

article h1 {
    margin: 18px;
    // margin: sus_si_jos stanga_si_dreapta;
    // margin: sus dreapta jos stanga;
}

h1 {
    font-weight: 900;
}

header a {
    animation: Razvan 1s;
}
// @keyframes Razvan {
    // 0% {
        // transform: translateY(-300px);
    // }
// }

div a:hover {
    background-color: #2d3436;
}

nav a:hover {
    transition: all 0.3s ease-in;
    // text-align: center;
    // padding: 15px;
    color: $alb;
    background-color: $negru;
}

nav a {
    color: black;
    padding: 20px 20px;
}

footer {
    animation: Iulian 2s;
}
@keyframes Iulian {
    0% {
        transform: translateY(-50px);
    }
}

nav h3 {
    padding: 10px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

nav a {
    padding: 15px;
}

nav button {
    padding: 5px;
}

nav button {
    display: grid;
    animation: Schifirnet 2s;
}
@keyframes Schifirnet {
    0% {
        transform: translateY(-50px);
    }
}

nav button {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

article {
    padding: 25px;
}

p {
    text-align: start;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}